<template>
    <el-form  label-width="80px" label-position="left">
        <el-row type="flex" justify="space-between">
            <el-col :span="24" align="right">
                <el-button @click="back">返 回</el-button>
            </el-col>
        </el-row>
        <br>
        <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    size="mini" 
                    border
                    v-loading="loadingTable" 
                    element-loading-text="拼命加载中"
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="" label="业务周期" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="总人数" align="center" width="80px"> 
                    </el-table-column>
                    <el-table-column prop="" label="总订单数" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="总业绩" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="VIP辅导奖" align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="零售奖"  align="center" width="80px">
                    </el-table-column>
                    <el-table-column prop="" label="市场拓展奖(PV)" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="拓展奖占比" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="领导奖(PV))" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="领导奖占比" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="特别奖(PV)" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="特别奖占比" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="总奖金(PV)" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="总奖金拨出率" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="币种" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="总奖金" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="审核人" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="审核状态" align="center">
                    </el-table-column>
                    <el-table-column prop="" label="审核时间" align="center">
                    </el-table-column>
                    <el-table-column label="操作" align="center" width="90px">
                        <template slot-scope="scope">
                            <el-button type="text"  @click="showDialogChange(scope.row,scope.$index)">审核</el-button>
                        </template>
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //加载列表
            form: {
                id: "", //会员编号
                name: "", //姓名
            },
            //列表数据
            tableData: [],
            //分页数据
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
        };
    },
    methods: {
        //改变页数
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            //this.onSearch();
        },
        //每页条数改变
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            //this.onSearch();
        },
        //点击审核查看详情
        onShowDetails(data) {
            
        },
        //返回上一页
        back() {
            this.$router.go(-1);
        }
        
    },
    created() {
        
    }
};
</script>
<style scoped>
.top-title span{
    display: inline-block;
    margin-right: 30px;
}
</style>




