<template>
    <el-form  label-width="80px" label-position="left">
        <el-row>
            <el-col :span="24">
                <el-row type="flex" justify="center">
                    <el-col :span="4">
                        <el-form-item label="业务周期">
                            <el-date-picker v-model="periodCode" value-format="yyyyMM" format="yyyyMM" type="month" placeholder="选择日期"></el-date-picker>
                        </el-form-item>
                    </el-col>
                    <el-col :span="5" :offset="1" >
                        <el-button type="primary" @click="onSearch">查 询</el-button>
                    </el-col>
                </el-row>
            </el-col>
        </el-row>
        
       <el-row>
            <el-col :span="24">
                <el-table 
                    :data="tableData" 
                    size="mini" 
                    v-loading="loadingTable" 
                    element-loading-text="拼命加载中"
                    element-loading-spinner="el-icon-loading">
                    <el-table-column prop="periodCode" label="计算周期" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="valBefoerDesc" label="切换前状态类型" align="center" width="160px"> 
                    </el-table-column>
                    <el-table-column prop="valBefoer" label="切换前状态" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="valAfterDesc" label="切换后状态类型" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="valAfter" label="切换后状态" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="updateTime" label="修改时间" sortable align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="updateBy" label="切换人" align="center" width="160px">
                    </el-table-column>
                    <el-table-column prop="updateMemo" label="修改备注" align="center">
                    </el-table-column>
                </el-table>
            </el-col>
        </el-row>
        <br>
        <el-row type="flex" justify="center">
            <el-col :span="8" align="center">
                <el-pagination
                    :page-size="pageData.pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :page-sizes="[10, 20, 30, 50,pageData.total]"
                    :total="pageData.total"
                    :current-page="pageData.currentPage"
                    @current-change="onChangePage"  
                    @size-change="handleSizeChange">
                </el-pagination>
            </el-col>
        </el-row>
    </el-form>
</template>


<script>
export default {
    data() {
        return {
            loadingTable:false, //加载列表
            form: {
                id: "", //会员编号
                name: "", //姓名
            },
            //列表数据
            tableData: [],
            //分页数据
            pageData:{
                currentPage:1,
                pageSize:10,
                total:null,
            },
            //周期
            periodCode:""
        };
    },
    methods: {
        //查询所有业务周期
        onSearch() {
            this.loadingTable = true;  
            this.$axios({
                method:'post',
                url:"/apis/member/findPeriodLogAll",
                params:{
                    periodCode:this.periodCode,
                    currentPage:this.pageData.currentPage,
                    pageSize:this.pageData.pageSize,
                    date:new Date().getTime()
                }
            })     
            .then(response=>{
                console.log(response.data)
                if(response.data.code){
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.pageData.total = response.data.data.total;
                    let list = response.data.data.list;
                    // for(let i= 0; i < list.length; i++){
                    //     //奖金状态
                    //     if(list[i].calStatus==0){
                    //         list[i].calStatus = "未开始";
                    //     }else if(list[i].calStatus==1){
                    //         list[i].calStatus = "计算中";
                    //     }else if(list[i].calStatus==2){
                    //         list[i].calStatus = "临时发布核对中";
                    //     }else if(list[i].calStatus==3){
                    //         list[i].calStatus = "正式发布";
                    //     }
                    //     //业绩状态
                    //     if(list[i].salesStatus==0){
                    //         list[i].salesStatus = "未开始";
                    //     }else if(list[i].salesStatus==1){
                    //         list[i].salesStatus = "已开始";
                    //     }else if(list[i].salesStatus==2){
                    //         list[i].salesStatus = "外部关闭补录中";
                    //     }else if(list[i].salesStatus==3){
                    //         list[i].salesStatus = "已关闭";
                    //     }
                    //     //发放状态
                    //     if(list[i].bonusStatus==0){
                    //         list[i].bonusStatus = "未放出";
                    //     }else if(list[i].bonusStatus==1){
                    //         list[i].bonusStatus = "已放出";
                    //     }
                    // }
                    this.pageData.currentPage = response.data.data.pageNum;
                    this.pageData.pageSize = response.data.data.pageSize;
                    this.tableData = response.data.data.list;
                }
                setTimeout(()=>{
                    this.loadingTable = false;
                },200)
            })
        },
        //改变页数
        onChangePage(currentPage) {
            this.form.currentPage = currentPage;
            this.onSearch();
        },
        //每页条数改变
        handleSizeChange(pageSize) {
            this.pageData.pageSize = pageSize;
            this.onSearch();
        }
    },
    created() {
        this.onSearch();
    }
};
</script>




